import IdsProvider from '../services/IdsProvider';
import { scaffoldStore } from 'undo-redo-vuex';
import { copy } from '../utils';

const pinNode = (node)=>{
    node.pinned = true;
    node.fx = node.x;
    node.fy = node.y;
};
const unpinNode = (node)=>{
    node.pinned = false;
    node.fx = null;
    node.fy = null;
};

const state = {
    nodes: [],
    links: [],
    selection: {
        links: {},
        nodes: {}
    },
    initNodes:[],
    initLinks:[]
};

const mutations = {
    emptyState: state => {
        state.nodes = [...state.initNodes];
        state.links = [...state.initLinks];
    },
    addNode(state, node) {
        state.nodes.push(node);
    },
    updateNode(state, {node, id}) {
        // todo redo to usage id not as a nodes array index
        state.nodes[id] = node
    },
    removeSelected(state, payload) {
            Object.keys(state.selection.nodes).forEach((id) => {
                state.nodes = state.nodes.filter(node => node.id.toString() !== id.toString());
                state.links = state.links.filter((link) => {
                    return link.sid.toString() !== id.toString() && link.tid.toString() !== id.toString()
                });
            });
            Object.keys(state.selection.links).forEach((id) => {
                state.links = state.links.filter(link => link.id.toString() !== id.toString());
            });
    },
    selectNode(state, nodeId){
        const node = state.nodes.find((node)=>node.id === nodeId);
        if (!node) return;

        state.selection.nodes = {[node.id]:node};
    },
    addLink: (state, link) => {
        state.links.push(link);
    },
    updateLink(state, {link, id}) {
        state.links[id] = link;
    },
    selectLink(state, link) {
        if (state.selection.links[link.id]) {
            delete state.selection.links[link.id];
        } else {
            state.selection.links = {...state.selection.links, [link.id]:link};
        }
    },
    resetSelection(state) {
        state.selection.nodes = {};
        state.selection.links = {};
    },
    setData(state, { nodes = [], links = [] }) {
        IdsProvider.nextNodeId = nodes.reduce((max, { id }) => id > max ? id : max, 0);
        state.links = copy(links);
        state.nodes = copy(nodes);
        state.initLinks = copy(links);
        state.initNodes = copy(nodes);
    },
    pinNode(state, nodeId) {
        const node = state.nodes[nodeId];
        if (!node) {
            return
        }
        pinNode(node);
    },
    unpinNode(state, nodeId){
        const node = state.nodes[nodeId];
        if (!node) {
            return
        }
        unpinNode(node);
    },
    pinAllNodes(state){
        state.nodes.forEach((node)=>{
            pinNode(node)
        })
    },
    unpinAllNodes(state){
        state.nodes.forEach((node)=>{
            unpinNode(node)
        })
    }
};

const getters = {
  getNode: ({ nodes }) => (id) => nodes[id]
};

const actions = {
   setGraph({ commit, dispatch }, data) {
       dispatch('clear', {});
       commit('setData', data);
   }
};

export default scaffoldStore({
    state,
    actions,
    mutations,
    getters,
    namespaced: true,
    debug: true
});
