<template>
    <v-dialog v-model="createDlg" scrollable max-width="400px">
        <v-card v-on:drop.prevent.stop="drop" v-on:dragover="$event.preventDefault()">
            <v-card-title>New</v-card-title>

            <v-form v-model="valid">
                <v-card-text style="height: 300px;">
                    <v-text-field label="Name" v-model.lazy="graph.name" :rules="rules"/>
                    <v-file-input label="Select file" accept="application/json" @change="onChange" v-model="file">
                    </v-file-input>
                </v-card-text>
            </v-form>

            <v-divider/>

            <v-card-actions>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
    import { mapActions, mapMutations } from 'vuex';
    import { parse, isExtJSON, getEmptyGraph } from '../utils';

    export default {
        name: 'vh-create-dlg',
        data: () => ({
            valid: false,
            graph: getEmptyGraph(),
            file: null,
            rules: [ val => !!val || 'Required.' ]
        }),
        computed: {
            createDlg: {
                get() {
                    return this.$store.state.app.createDlg;
                },
                set(val) {
                    this.toggleCreateDlg(val);
                }
            }
        },
        methods: {
            ...mapActions('graphs', [
                'create'
            ]),
            ...mapMutations('app', [
                'toggleCreateDlg'
            ]),
            save() {
                this.create(this.graph);
                this.close();
            },
            close() {
                this.graph = {};
                this.createDlg = false;
            },
            async onChange(file) {
                if (!file) {
                    return;
                }
                let parsed;
                try {
                    parsed = await parse(file);
                } catch (e) {
                    console.error(e);
                    return;
                }
                const { name, nodes = [], links = [] } = parsed;
                this.graph = { name, nodes, links }
            },
            drop(e) {
                e.preventDefault();
                e.stopPropagation();
                const [ file ] = e.dataTransfer.files;
                if (!isExtJSON(file)) {
                    return;
                }
                this.file = file;
                void this.onChange(file);
            }
        },
    }
</script>
