import Vue from 'vue';
import Vuex from 'vuex';
import currentGraph from './currentGraph';
import graphs from './graphs';
import app from './app';
import auth from './auth';
import undoRedo from 'undo-redo-vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    plugins: [
        undoRedo({
            paths: [
                {
                    namespace: "currentGraph",
                    ignoreMutations: ['emptyState', 'selectNode', 'selectLink', 'resetSelection', 'setData'],
                },
            ],
        }),
    ],
    modules: {
        graphs,
        currentGraph,
        app,
        auth,
    }
})
