<template>
    <v-app-bar app clipped-left>
        <v-app-bar-nav-icon v-if="authenticated" @click.stop="toggleSidebar"/>

        <v-toolbar-title v-if="!editing" @click="authenticated && edit()">
            {{ selectedName }}
        </v-toolbar-title>

        <v-text-field v-if="authenticated && editing"
                      dense
                      solo
                      hide-details
                      @keyup.stop="onKeyUp"
                      label="name"
                      append-icon="mdi-check"
                      @click.stop="$event.preventDefault()"
                      @click:append.stop.prevent="save"
                      v-model="editing.name">
        </v-text-field>

        <v-spacer/>

        <v-tooltip bottom v-if="authenticated">
            <template v-slot:activator="{ on }">
                <v-btn class="ma-2" outlined color="blue" small v-on="on" @click.stop="toggleCreateDlg">
                    <v-icon color="blue lighten-1">mdi-plus</v-icon>
                </v-btn>
            </template>
            <span>New</span>
        </v-tooltip>

        <v-tooltip bottom v-if="authenticated">
            <template v-slot:activator="{ on }">
                <v-btn class="ma-2" :disabled="loading" v-on="on" outlined color="primary" small @click.stop="saveCurrent">
                    <v-icon>mdi-content-save</v-icon>
                </v-btn>
            </template>
            <span>Save current</span>
        </v-tooltip>

        <v-tooltip bottom>
            <template v-slot:activator="{ on }">
                <v-btn class="ma-2" v-on="on" outlined small color="accent" @click.stop="downloadCurrent">
                    <v-icon color="blue">mdi-download</v-icon>
                </v-btn>
            </template>
            <span>Download file</span>
        </v-tooltip>

        <template>
            <v-tooltip bottom v-if="authenticated">
                <template v-slot:activator="{ on }">
                    <v-btn class="ma-2" v-on="on" outlined small color="accent" @click.stop="signOut">
                        <v-icon color="blue">mdi-logout-variant</v-icon>
                    </v-btn>
                </template>
                <span>Sign out</span>
            </v-tooltip>

            <v-tooltip bottom v-else>
                <template v-slot:activator="{ on }">
                    <v-btn class="ma-2" v-on="on" outlined small color="accent" @click.stop="signIn">
                        <v-icon color="blue">mdi-login-variant</v-icon>
                    </v-btn>
                </template>
                <span>Sign in</span>
            </v-tooltip>
        </template>
    </v-app-bar>
</template>

<script>
    import { app, authService } from "../firebase";
    import { downloadObjectAsJson } from "../utils";
    import { mapMutations, mapGetters, mapState, mapActions } from 'vuex';

    export default {
        name: 'vh-header',
        data: () => ({
            editing: null,
        }),
        computed: {
            ...mapGetters('graphs', ['selectedName']),
            ...mapGetters('auth', ['authenticated']),
            ...mapState('app', ['loading']),
        },
        methods: {
            ...mapMutations('app', [
                'toggleSidebar',
                'toggleCreateDlg',
            ]),
            ...mapActions('graphs', ['saveCurrent', 'updateCurrentGraphName']),

            downloadCurrent() {
                const { nodes, links } = this.$store.state.currentGraph;
                const { name, id } = this.$store.state.graphs.items[this.$store.state.graphs.selectedId];
                downloadObjectAsJson({ nodes, links, name, id }, name);
            },
            signIn() {
                authService.signInWithPopup(new app.auth.GoogleAuthProvider())
            },
            signOut() {
                authService.signOut();
            },
            edit() {
                this.editing = { name: this.selectedName };
                setTimeout(() => addEventListener('click', this.close));
            },
            close() {
                this.editing = null;
                removeEventListener('click', this.close);
            },
            async save() {
                await this.updateCurrentGraphName(this.editing.name);
                this.close();
            },
            onKeyUp(event) {
                if (event.key === 'Escape') {
                    this.close();
                }
                if (event.key === 'Enter') {
                    this.save();
                }
            },
        },
        mounted() {
            this.close = this.close.bind(this);
        }
    }
</script>
