import graphService from '../services/GraphService';

const state = {
    items: {},
    selectedId: null,
    preview: null,
};

const getters = {
    list: ({ items }) => Object.values(items),
    selected: ({ items, selectedId }) => items[selectedId],
    selectedName: ({ items, selectedId }) => (selectedId && items[selectedId].name) || 'Unnamed',
    lastUpdated: ({ items }) => Object.values(items).reduce((item, next) => next.updated > item.updated ? next : item),
    previewImage: ({ items, preview }) => preview && items[preview] && items[preview].image,
};

const mutations = {
    setItems: (state, graphs) => state.items = graphs.reduce((acc, item) => {
        acc[item.id] = item;
        return acc;
    }, {}),
    setItem: (state, item) => {
        state.items = {...state.items, [item.id]: item}
    },
    select: (state, id) => state.selectedId = id,
    updateSelected: ({selectedId: id, items}, data) => items[id] = {...items[id], ...data},
    setScreenShotFunc: (state, func) => state.screenShot = () => {
        return new Promise((res, rej) => func((err, url) => {
            err ? rej(err) : res(url);
        }))
    },
    setPreview: (state, id) => state.preview = id,
    removeItem: (state, id) => {
        const items = { ...state.items };
        delete items[id];
        state.items = items;
    }
};

const actions = {
    async loadItems({ commit, dispatch }) {
        commit('setItems', await graphService.get());
        dispatch('selectLastUpdated');
    },
    selectLastUpdated({ dispatch, getters }) {
        dispatch('selectGraph', getters.lastUpdated);
    },
    selectGraph({ commit, dispatch }, item) {
        commit('select', item.id);
        dispatch('currentGraph/setGraph', item, { root: true });
    },
    async saveCurrent({ commit, state, rootState: { currentGraph: { nodes, links } } }) {
        commit('app/toggleLoading', true, { root: true });
        const image = await state.screenShot();
        await graphService.updateItem({ nodes, links, image, id: state.selectedId });
        commit('updateSelected', { nodes, links, image });
        commit('app/toggleLoading', false, { root: true });
    },
    async updateCurrentGraphName({ commit, state }, name) {
        commit('app/toggleLoading', true, { root: true });
        const data = { id: state.selectedId, name };
        try {
            await graphService.updateItemName(data);
        } catch (e) {
            console.error(e);
        } finally {
            commit('app/toggleLoading', false, { root: true });
        }
        commit('updateSelected', data);
    },
    async create({ commit }, data) {
        commit('app/toggleLoading', true, { root: true });
        try {
            commit('setItem', await graphService.createItem(data));
        } catch (e) {
            console.error(e);
        } finally {
            commit('app/toggleLoading', false, { root: true });
        }
    },
    clearItems({ commit, dispatch }) {
        commit('setItems', []);
        dispatch('selectGraph', {});
    },
    async removeItem({ commit, state, dispatch }, id) {
        commit('app/toggleLoading', true, { root: true });
        try {
            await graphService.remove(id);
        } catch (e) {
            console.error(e);
            return;
        } finally {
            commit('app/toggleLoading', false, { root: true });
        }
        commit('removeItem', id);
        if (state.selectedId === id) {
            dispatch('selectLastUpdated');
        }
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
