import { db, authService, app } from '../firebase';

const timestamp = () => app.firestore.Timestamp.now();

class GraphService {
    constructor() {
        this.collection = db.collection('graphs');
    }
    get() {
        const { currentUser } = authService;
        if (!currentUser) {
            throw Error('Current user is not defined!');
        }
        return this.collection
            .where('author', '==', currentUser.uid)
            .get()
            .then(({ docs}) => docs.map(doc => ({ id: doc.id, ...doc.data() })));
    }
    updateItem({ id, ...data }) {
        if (!id) {
            throw Error('ID is required!');
        }
        return this.collection.doc(id).update({ ...data, updated: timestamp() })
    }
    updateItemName({ id, name }) {
        return this.updateItem({ id, name })
    }
    createItem(data) {
        const { currentUser } = authService;
        if (!currentUser) {
            throw Error('Current user is not defined!');
        }
        const time = timestamp();
        return this.collection
            .add({ ...data, author: currentUser.uid, created: time, updated: time })
            .then(docRef => ({ id: docRef.id, ...data }));
    }
    remove(id) {
        return this.collection.doc(id).delete();
    }
}

const graphService = new GraphService();
export default graphService;
