export const parse = file => new Promise((resolve, reject) => {
    const fl = new FileReader();
    fl.addEventListener("loadend", e => resolve(JSON.parse(e.target.result)));
    fl.onerror = fl.onabort = reject;
    fl.readAsText(file);
});


const last = arr => arr[arr.length - 1];

const split = (sep, str) => str.split(sep);

export const isExtJSON = ({ name }) => last(split(".", name)) === "json";

export const getEmptyGraph = () => ({ nodes: [], links: [] });

export const copy = data => JSON.parse(JSON.stringify(data));

export const downloadObjectAsJson = (data, name) => {
    const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(data));
    const anchor = document.createElement('a');
    anchor.setAttribute("href",     dataStr);
    anchor.setAttribute("download", name + ".json");
    document.body.appendChild(anchor); // required for firefox
    anchor.click();
    anchor.remove();
};