import { authService } from "../firebase";

const toggleFn = prop =>
    (state, payload) => state[prop] = typeof payload === 'boolean' ? payload : !state[prop];

const app = {
    namespaced: true,
    state: {
        sidebar: false,
        createDlg: false,
        loading: false,
    },
    mutations: {
        toggleSidebar: toggleFn('sidebar'),
        toggleCreateDlg: toggleFn('createDlg'),
        toggleLoading: toggleFn('loading'),
    },
    actions: {
        start({ dispatch, commit }) {
            authService.onAuthStateChanged(user => {
                commit('auth/setData', user, { root: true });
                if (user) {
                    dispatch('graphs/loadItems', null, { root: true });
                } else {
                    dispatch('graphs/clearItems', null, { root: true });
                }
            })
        }
    }
};

export default app;
