<template>
    <v-navigation-drawer app v-model="sidebar" clipped>
        <v-list flat>
            <v-list-item v-for="graph in list"
                         :key="graph.id"
                         active-class="active"
                         exact-active-class="active"
                         :class="selectedId === graph.id ? 'active' : null"
                         @click="selectGraph(graph)">
                <v-list-item-title @mouseover="setPreview(graph.id)"
                                   @mouseleave="setPreview(null)">
                    {{ graph.name }}
                </v-list-item-title>
                <v-list-item-action>
                    <v-btn icon @click.stop="remove(graph.id)">
                        <v-icon>mdi-delete-outline</v-icon>
                    </v-btn>
                </v-list-item-action>
            </v-list-item>
        </v-list>
    </v-navigation-drawer>
</template>

<script>
    import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';

    export default {
        name: 'vh-sidebar',
        data: () => ({ editing: {}, showPreviewFor: null }),
        computed: {
            ...mapState('graphs', ['selectedId']),
            ...mapGetters('graphs', ['selected', 'list']),
            sidebar: {
                get() {
                    return this.$store.state.app.sidebar;
                },
                set(val) {
                    this.toggleSidebar(val);
                },
            }
        },
        methods: {
            ...mapMutations('app', ['toggleSidebar']),
            ...mapActions('graphs', ['selectGraph', 'removeItem']),
            ...mapMutations('graphs', ['setPreview']),
            async remove(id) {
                const res  = await this.$confirm('Do you really want to remove this item?');
                if (res) {
                    this.removeItem(id);
                }
            }
        },
    }
</script>

<style>
    .v-list .v-list-item.active .v-list-item__title {
        color: #1ec6a2;
    }
</style>
