<template>
    <div class="net-wrap">
        <v-toolbar dense bottom absolute flat>
            <v-toolbar-items>
                <v-btn @click="reset">
                    <v-icon>mdi-fit-to-page</v-icon>
                </v-btn>
                <v-btn @click="undo" :disabled="!canUndo">
                    <v-icon>mdi-undo</v-icon>
                </v-btn>
                <v-btn @click="redo" :disabled="!canRedo">
                    <v-icon>mdi-redo</v-icon>
                </v-btn>
            </v-toolbar-items>
        </v-toolbar>
        <v-dialog v-model="dialog"  max-width="400px">
            <v-card>
                <v-card-title>
                    <v-text-field @focus="$event.target.select()"
                                  label="name"
                                  v-model="selection.nodes[editingNodeId].name"
                                  ref="nodeNameInput"
                                  v-if="selection.nodes[editingNodeId]"
                                  @keypress.enter="dialog=false"/>
                </v-card-title>
            </v-card>
        </v-dialog>
        <div @click="onClick"
             @keydown.delete="onDeletePress"
             @keypress.enter="edit"
             tabindex="0"
             v-if="!hidden"
             class="net-wrap">
            <d3-network ref="net" :net-nodes="nodes" :net-links="links" :options="options" :selection="selection"
                        @node-click="onNodeClick" @link-click="onLinkClick"/>
        </div>
    </div>
</template>

<script>
    import D3Network from 'vue-d3-network'
    import {mapActions, mapMutations, mapState, mapGetters} from "vuex";
    import IdsProvider from "../services/IdsProvider";

    export default {
        name: 'graph',
        components: {
            D3Network,
        },
        data: () => ({
            options:
                {
                    force: 3000,
                    nodeSize: 20,
                    nodeLabels: true,
                    linkLabels: true,
                    linkWidth: 1,
                },
            dialog: false,
            editingNodeId: "0",
            emulationEnabled: true,
            hidden: false,
        }),
        computed: {
            ...mapState('currentGraph', [
                'nodes',
                'links',
                'selection',
                'canUndo',
                'canRedo'
            ]),
            ...mapGetters('currentGraph', ['getNode']),
            ...mapGetters('graphs', ['selected']),
        },
        watch: {
            selected(val) {
                val && this.reset();
            },
            dialog(val) {
                if (!val) {
                    return;
                }
                // dirty dirty hack. Hope once day Vuetify will fix it
                this.$nextTick(()=>{
                    this.$nextTick(()=> {
                        this.$refs.nodeNameInput.focus();
                    });
                });
          }
        },
        methods: {
            ...mapMutations('currentGraph', [
                'resetSelection',
                'selectNode',
                'selectLink',
                'addNode',
                'addLink',
                'removeSelected',
                'pinNode',
                'unpinNode',
                'pinAllNodes',
                'unpinAllNodes'
            ]),
            ...mapMutations('graphs', ['setScreenShotFunc']),
            ...mapActions('currentGraph', [
                'undo',
                'redo',
                'clear'
            ]),
            onNodeClick(event, node) {
                if (event.shiftKey) {
                    this.selectNode(node.id);
                } else if (event.ctrlKey || event.metaKey) {
                    Object.keys(this.selection.nodes).forEach(id => this.addLink({sid: id, tid: node.id}));
                    this.resetSelection();
                } else {
                    this.resetSelection();
                    this.selectNode(node.id);
                }
            },
            onLinkClick(event, link) {
                if (event.shiftKey) {
                    this.selectLink(link);
                } else {
                    this.resetSelection();
                    this.selectLink(link);
                }

            },
            onClick(event) {
                if (event.target.tagName === 'svg') {
                    if (event.ctrlKey || event.metaKey) {
                        const nodeId = IdsProvider.generateNextNodeId();
                        this.addNode({x: event.x, y: event.y, id: nodeId});
                        Object.keys(this.selection.nodes).forEach(id => this.addLink({sid: id, tid: nodeId}));
                        this.selectNode(nodeId);
                        this.editingNodeId = nodeId;
                        this.dialog = true;
                        return;
                    }
                    this.resetSelection();
                }
            },
            edit() {
                const selectedNodes = Object.keys(this.selection.nodes);
                if (selectedNodes.length === 1) {
                    this.editingNodeId = selectedNodes[0];
                    this.dialog = true;
                }
            },
            onDeletePress() {
                this.removeSelected({});
                this.resetSelection();
            },
            reset() {
                this.hidden = true;
                this.$nextTick(() => this.hidden = false);
            }
        },
        mounted() {
            this.setScreenShotFunc((cb) => this.$refs.net.$refs.svg.svgScreenShot(cb));
        }
    };
</script>
<style src="./graph.css">
</style>
